<template>
  <DataTable
    title="История операций"
    name="history"
    :columns="columns"
    :headings="headings"
    :adapter="adapter"
    :request-function="requestFunction"
  />
</template>

<script>
import http from "@/store/helpers/http";
import DataTable from "@/components/DataTable";

export default {
  components: {
    DataTable
  },
  data() {
    return {
      columns: ["date", "coming", "outgo", "operation_type_name"],
      headings: {
        date: "Дата",
        coming: "Приход",
        outgo: "Расход",
        operation_type_name: "Операция"
      },
      adapter: response => {
        const { selected_month_id, years, history } = response.data;
        const { results, pages_count } = history;
        return {
          data: results || [],
          count: pages_count || 0
        };
      },
      requestFunction: data => {
        const reqFilters = str => {
          const operations = [
            {
              word: "пополнение баланса",
              res: "balance"
            },
            {
              word: "покупка подписки",
              res: "subscription"
            },
            {
              word: "обращение к API",
              res: "api"
            }
          ];
          const filtered = operations.find(item =>
            item.word.toLowerCase().includes(str.toLowerCase())
          );
          return str && filtered ? filtered.res : "";
        };
        const { month_id, limit, page, query } = data;
        const params = {
          month_id,
          page,
          page_size: limit,
          operation_type: reqFilters(query)
        };
        return http.get("/operations/", {
          params
        });
      }
    };
  }
};
</script>
