import http from "@/store/helpers/http";
import { SET_RATES } from "@/store/mutation-types";

const state = {
  rates: []
};
const getters = {};
const actions = {
  async getRates({ commit }) {
    try {
      const response = await http.get("/rates/");
      const rates = response.data;
      commit(SET_RATES, rates);
    } catch (error) {
      console.error(error);
    }
  }
};
const mutations = {
  [SET_RATES](state, payload) {
    state.rates = payload;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
