<template>
  <DataTable
    title="Статистика"
    name="statistic"
    :columns="columns"
    :headings="headings"
    :adapter="adapter"
    :request-function="requestFunction"
  />
</template>

<script>
import http from "@/store/helpers/http";
import moment from "moment";
import DataTable from "@/components/DataTable";
import transactionHistoryData from "@/data/transaction-history-data.json";

export default {
  components: {
    DataTable
  },
  data() {
    return {
      columns: [
        "path",
        "http_method",
        "description",
        "requests_count",
        "month_id"
      ],
      headings: {
        path: "Путь",
        http_method: "Метод",
        description: "Описание",
        requests_count: "Всего запросов",
        month_id: "Месяц"
      },
      adapter: response => {
        const { selected_month_id, years, statistics } = response.data;
        const { results, pages_count } = statistics;
        results.forEach(element => {
          const date = new Date(
            `${element.month_id.slice(0, 4)} ${element.month_id.slice(4)}`
          );
          moment.locale("ru");
          element.month_id = moment(date.toISOString()).format("MMMM YYYY");
        });
        return {
          data: results || [],
          count: pages_count || 0
        };
      },
      requestFunction: data => {
        const reqFilters = val => {
          const id = Number.parseInt(val);
          return !Number.isNaN(id) ? id : "";
        };
        const { month_id, limit, page, query } = data;
        const params = {
          month_id,
          page,
          page_size: limit,
          api_method_id: reqFilters(query)
        };
        return http.get("/statistics", {
          params
        });
      }
    };
  }
};
</script>
