<template>
  <div class="">
    <div v-show="state.boolean == true">
      <div v-if="myProgress == 100" class="sending-request-text">
        <d-progress
          :max="100"
          :value="myProgress"
          height="15px"
          class="mb-2"
          :animated="true"
          :theme="state.string.success"
        />
        Ваша заявка отправлена!
      </div>
    </div>

    <d-form
      v-show="state.boolean == null || state.boolean == false"
      class="form-modal"
    >
      <d-row>
        <d-col sm="12" lg="9">
          <p>
            <strong
              >Прежде чем подавать заявку в поддержку, ознакомьтесь со
              справочной информацией</strong
            >
          </p>
        </d-col>
      </d-row>
      <d-row class="mb-4">
        <d-col sm="12" lg="2">
          <d-link>FAQ</d-link>
        </d-col>
        <d-col sm="12" lg="5">
          <d-link>Документация к API</d-link>
        </d-col>
      </d-row>
      <d-form-row>
        <d-col md="12" class="form-group">
          <label for="heading">Заголовок</label>
          <d-input
            id="heading"
            v-model="form.title"
            type="text"
            name="title"
            placeholder="Короткое описание вашего обращения"
          />
        </d-col>
      </d-form-row>

      <d-form-row>
        <d-col md="12" class="form-group">
          <label for="description">Описание</label>
          <d-textarea
            id="description"
            v-model="form.description"
            style="min-height: 100px;"
            name="description"
          />
        </d-col>
      </d-form-row>

      <d-form-row>
        <d-col md="12" class="form-group">
          <label for="file">Загрузить файл</label>
          <div class="dropzone-wrapper">
            <VueDropzone
              id="file"
              ref="dropzone"
              class="dropzone"
              :options="dropzoneOptions"
              :include-styling="false"
              @vdropzone-file-added="vfileAdded"
              @vdropzone-complete="afterComplete"
              @vdropzone-sending-multiple="sendingEvent"
              @vdropzone-removed-file="vremoved"
              @vdropzone-total-upload-progress="vprogress"
            />
          </div>
          <div v-if="state.supported_type == false" class="">
            Что то пошло не так... Поддерживаемые форматы .jpg, .jpeg, .pdf,
            .docx
          </div>
          <div v-else-if="state.filesize_normal == false" class="">
            Файл должен весить меньше 2-ух мегабайт
          </div>
          <div
            v-else-if="state.isSendButton && myProgress <= 100"
            class="sending-request-text"
          >
            <d-progress
              :max="100"
              :value="myProgress"
              height="15px"
              class="mb-2"
              :animated="true"
              :theme="state.string.warning"
            />
            Ваша заявка отправляется...
          </div>
        </d-col>
      </d-form-row>
      <d-button
        v-if="
          state.filesize_normal == true &&
            state.supported_type == true &&
            state.count_files > 0 &&
            this.form.title.length > 0 &&
            this.form.description.length > 0
        "
        type="button"
        @click="triggerSend()"
      >
        Отправить
      </d-button>
      <d-button v-else type="button" disabled>
        Отправить
      </d-button>
      <d-button theme="danger" class="ml-3" @click.prevent="$emit('close')">
        Отмена
      </d-button>
    </d-form>
  </div>
</template>

<script>
import VueDropzone from "vue2-dropzone";

const getTemplate = () => `
<div class="dz-preview dz-file-preview">
  <div class="dz-image">
    <div data-dz-thumbnail-bg></div>
  </div>
  <div class="dz-details">
    <div class="dz-size"><span data-dz-size></span></div>
    <div class="dz-filename"><span data-dz-name></span></div>
  </div>
  <div class="dz-progress"><span class="dz-upload" data-dz-uploadprogress></span></div>
  <div class="dz-error-message"><span data-dz-errormessage></span></div>
  <div class="dz-success-mark"><i class="fa fa-check"></i></div>
  <div class="dz-error-mark"><i class="fa fa-close"></i></div>
</div>
`;

export default {
  name: "TheSupport",
  components: {
    VueDropzone
  },
  data() {
    return {
      files: [],
      topic: 1,
      myProgress: 0,
      types_of_file: ["png", "jpeg", "jpg", "docx"],
      state: {
        boolean: null,
        filesize_normal: null,
        supported_type: null,
        count_files: null,
        isSendButton: null,
        string: {
          success: "success",
          warning: "warning"
        }
      },
      dropzoneOptions: {
        headers: {
          Authorization: `${localStorage.getItem("user-token")}`,
          "Cache-Control": null,
          "X-Requested-With": null
        },
        autoProcessQueue: false,
        url: "/support/create/",
        maxFilesize: 2, // MB
        uploadMultiple: true,
        maxFiles: 4,
        addRemoveLinks: true,
        previewTemplate: getTemplate(),
        parallelUploads: 4
      },
      form: {
        title: "",
        description: ""
      }
    };
  },
  mounted() {
    this.replaceInput();
  },
  methods: {
    replaceInput() {
      let form_modal = document.getElementsByClassName("form-modal")[0];
      let list = document.getElementsByClassName("dz-hidden-input");

      for (let item of list) {
        form_modal.appendChild(item);
      }
    },
    triggerSend() {
      this.$refs.dropzone.processQueue();
      this.state.isSendButton = true;
    },
    sendingEvent(file, xhr, formData) {
      formData.append("title", this.form.title);
      formData.append("description", this.form.description);
      for (let element in file) {
        formData.append("files", file[element]);
      }
    },
    afterComplete(file) {
      file.status == "success"
        ? (this.state.boolean = true)
        : (this.state.boolean = false);
      console.log(file);
    },
    vfileAdded(file) {
      file.upload.total < 2000000
        ? (this.state.filesize_normal = true)
        : (this.state.filesize_normal = false);
      let extension_of_file = this.getExtension(file.upload.filename);
      this.types_of_file.includes(extension_of_file)
        ? (this.state.supported_type = true)
        : (this.state.supported_type = false);
      this.countFiles();
      this.$nextTick(() => {
        this.replaceInput();
      });
    },
    vprogress(totalProgress, totalBytes, totalBytesSent) {
      this.myProgress = Math.floor(totalProgress);
    },
    vremoved(file, xhr, error) {
      this.countFiles();
    },
    getExtension(filename) {
      let parts = filename.split(".");
      return parts[parts.length - 1];
    },
    countFiles() {
      this.state.count_files = this.$refs.dropzone.dropzone.files.length;
      this.files = this.$refs.dropzone.dropzone.files;
      console.log(this.files);
    }
  }
};
</script>

<style lang="css">
.dz-success-mark,
.dz-error-message span {
  display: none;
}
.dz-remove {
  align-self: center;
}
.sending-request-text {
  text-align: center;
}
</style>
