import http from "@/store/helpers/http";
import { SET_PAYMENT_LINK, SET_INVOICE_LINK } from "@/store/mutation-types";

const state = {
  link: "",
  invoiceLink: ""
};

const getters = {};

const actions = {
  async getPaymentLink({ commit, state }, payload) {
    const formData = new FormData();
    formData.set("amount", payload);
    try {
      const respone = await http.post("/payment-link-checkout/", formData);
      const { pay_link } = respone.data;
      commit(SET_PAYMENT_LINK, pay_link);
      if (state.link) {
        location.assign(state.link);
      }
    } catch (error) {
      console.error(error);
    }
  },
  async getInvoiceLink({ commit, state, rootGetters }, payload) {
    const formData = new FormData();
    formData.set("amount", payload);
    try {
      const respone = await http.post("/payment-link-invoice/", formData);
      const { invoice_link } = respone.data;
      commit(SET_INVOICE_LINK, invoice_link);
      if (state.invoiceLink) {
        location.assign(state.invoiceLink);
      }
    } catch (error) {
      console.error(error);
    }
  }
};

const mutations = {
  [SET_PAYMENT_LINK](state, payload) {
    state.link = payload;
  },
  [SET_INVOICE_LINK](state, payload) {
    state.invoiceLink = payload;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
