<template>
  <div class="mb-4">
    <div class="row page-header py-4 no-gutters">
      <div class="text-center text-sm-left mb-4 mb-sm-0 col-sm-4 col">
        <h3 class="page-title">Общие сведения</h3>
      </div>
    </div>
    <!-- Card Header -->
    <d-row>
      <d-col>
        <d-form>
          <d-form-row>
            <!-- First Name -->
            <d-col lg="6" md="12" sm="12" class="form-group">
              <d-card class="mb-3">
                <d-card-body>
                  <label for="feFirstName">Имя</label>
                  <d-form-input
                    id="feFirstName"
                    v-model="userName"
                    :state="isValidName"
                    class="mb-3"
                    type="text"
                    placeholder="First Name"
                    :disabled="!editable"
                  />
                  <div v-if="errorState && userData.userName">
                    <d-form-invalid-feedback
                      v-for="(item, index) in error"
                      v-show="errorState"
                      :key="index"
                      class="fields mb-3"
                      >{{ item.message }}</d-form-invalid-feedback
                    >
                  </div>
                  <div v-if="$v.userData.$error">
                    <d-form-invalid-feedback
                      v-show="!$v.userData.userName.required"
                      class="fields mb-3"
                      >Обязательное поле</d-form-invalid-feedback
                    >
                  </div>
                  <label for="feEmail">Электронная почта:</label>
                  <d-form-input
                    id="feEmail"
                    v-model="userEmail"
                    :state="isValidEmail"
                    class="mb-3"
                    type="email"
                    placeholder="Email Address"
                    :disabled="!editable"
                  />
                  <div v-if="errorState && userData.userEmail">
                    <d-form-invalid-feedback
                      v-for="(item, index) in error"
                      v-show="errorState"
                      :key="index"
                      class="fields mb-3"
                      >{{ item.message }}</d-form-invalid-feedback
                    >
                  </div>
                  <div v-if="$v.userData.userEmail.$error">
                    <d-form-invalid-feedback
                      v-show="!$v.userData.userEmail.email"
                      class="fields mb-3"
                      >Некорректный email</d-form-invalid-feedback
                    >
                  </div>
                  <div v-if="$v.userData.$error">
                    <d-form-invalid-feedback
                      v-show="!$v.userData.userEmail.required"
                      class="fields mb-3"
                      >Обязательное поле</d-form-invalid-feedback
                    >
                  </div>
                  <d-button
                    v-if="!editable"
                    size="sm"
                    theme="primary"
                    class="mb-2 mr-1"
                    @click.prevent="editable = !editable"
                  >
                    Редактировать
                  </d-button>
                  <d-button
                    v-if="editable"
                    size="sm"
                    theme="danger"
                    class="mb-2 mr-1"
                    @click.prevent="reset"
                  >
                    Отмена
                  </d-button>
                  <d-button
                    v-if="editable"
                    size="sm"
                    theme="success"
                    class="mb-2 mr-1"
                    @click.prevent="saveUser"
                  >
                    Сохранить
                  </d-button>
                </d-card-body>
              </d-card>
              <d-card class="mb-3">
                <d-card-body>
                  <fieldset>
                    <legend>Сменить пароль</legend>
                    <d-form-input
                      id="oldPass"
                      v-model="pass.oldPassword"
                      type="password"
                      placeholder="Старый пароль"
                      class="mb-3"
                      :state="isValidOld"
                      @blur.native="check('oldPassword')"
                    />
                    <div v-if="$v.pass.oldPassword.$error">
                      <d-form-invalid-feedback
                        v-show="!$v.pass.oldPassword.minLength"
                        class="fields mb-3"
                        >Введённый пароль слишком короткий. Он должен содержать
                        как минимум 8 символов.</d-form-invalid-feedback
                      >
                      <d-form-invalid-feedback
                        v-show="!$v.pass.oldPassword.required"
                        class="fields mb-3"
                        >Обязательное поле</d-form-invalid-feedback
                      >
                    </div>
                    <div v-if="validationOld">
                      <d-form-invalid-feedback
                        v-for="(item, index) in validationOld"
                        v-show="validationOld"
                        :key="index"
                        class="fields mb-3"
                        >{{ item }}</d-form-invalid-feedback
                      >
                    </div>
                    <d-form-input
                      id="newPass"
                      v-model="pass.newPassword"
                      type="password"
                      placeholder="Новый пароль"
                      class="mb-3"
                      :state="isValidNew"
                      @blur.native="check('newPassword')"
                    />
                    <div v-if="$v.pass.newPassword.$error">
                      <d-form-invalid-feedback
                        v-show="!$v.pass.newPassword.minLength"
                        class="fields mb-3"
                        >Введённый пароль слишком короткий. Он должен содержать
                        как минимум 8 символов.</d-form-invalid-feedback
                      >
                      <d-form-invalid-feedback
                        v-show="!$v.pass.newPassword.required"
                        class="fields mb-3"
                        >Обязательное поле</d-form-invalid-feedback
                      >
                    </div>
                    <div v-if="validationNew">
                      <d-form-invalid-feedback
                        v-for="(item, index) in validationNew"
                        v-show="validationNew"
                        :key="index"
                        class="fields mb-3"
                        >{{ item }}</d-form-invalid-feedback
                      >
                    </div>
                  </fieldset>

                  <d-button
                    size="sm"
                    theme="primary"
                    class="mb-2 mr-1"
                    @click.prevent="setPassword"
                  >
                    Сохранить изменения
                  </d-button>
                </d-card-body>
              </d-card>
              <d-card>
                <d-card-body title="Ключи">
                  <div class="mb-4">
                    <h6>API-ключ</h6>
                    <span>{{ apiKey }}</span>
                  </div>
                  <div class="mb-4">
                    <h6>Секретный ключ для стандартизации</h6>
                    <span>{{ secretKey }}</span>
                  </div>
                  <d-button
                    size="sm"
                    theme="primary"
                    class="mb-2 mr-1"
                    @click.prevent="refreshKeys"
                  >
                    Обновить
                  </d-button>
                </d-card-body>
              </d-card>
            </d-col>
            <d-col lg="6" md="12" sm="12" class="form-group">
              <d-card class="mb-3">
                <d-card-body title="У вас на счету">
                  <span>{{ balance || 0 }} ₽</span>

                  <div class="d-flex mt-3">
                    <d-button
                      size="sm"
                      theme="primary"
                      class="mb-2 mr-1"
                      @click.native="handlePayment"
                    >
                      Пополнить
                    </d-button>
                    <d-modal v-if="showPayment" @close="handleClose">
                      <d-modal-header>
                        <d-modal-title>Пополнить счет</d-modal-title>
                      </d-modal-header>
                      <d-modal-body>
                        <ThePayment />
                      </d-modal-body>
                    </d-modal>
                  </div>
                </d-card-body>
              </d-card>
              <d-card>
                <d-card-body title="Ваш тариф">
                  <h5>
                    Тариф «{{ rateName || "Тариф не выбран" }}», запросов
                    осталось {{ rateLimit }}.
                  </h5>

                  <d-button size="sm" theme="primary" class="mb-2 mr-1">
                    Сменить тариф
                  </d-button>
                  <a href="" class="d-block">Посмотреть все тарифы</a>
                </d-card-body>
              </d-card>
            </d-col>
          </d-form-row>
        </d-form>
      </d-col>
    </d-row>
  </div>
</template>

<script>
import ThePayment from "@/components/ThePayment";
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import { required, email, minLength } from "vuelidate/lib/validators";

export default {
  name: "TheUserAccountDetails",
  components: {
    ThePayment
  },
  data() {
    return {
      pass: {
        oldPassword: "",
        newPassword: ""
      },
      editable: false,
      showPayment: false
    };
  },
  validations: {
    pass: {
      oldPassword: {
        required,
        minLength: minLength(8)
      },
      newPassword: {
        required,
        minLength: minLength(8)
      }
    },
    userData: {
      userName: {
        required
      },
      userEmail: {
        required,
        email
      }
    }
  },
  computed: {
    ...mapState("user", [
      "fullName",
      "email",
      "balance",
      "apiKey",
      "secretKey",
      "rateName",
      "rateLimit"
    ]),
    ...mapState("user/checkoutErrors", ["error"]),
    ...mapGetters("user/checkoutErrors", [
      "errorState",
      "validationOld",
      "validationNew",
      "isValidBackend"
    ]),
    isValidOld() {
      return this.$v.pass.oldPassword.$error || this.validationOld
        ? "invalid"
        : this.pass.oldPassword
        ? "valid"
        : null;
    },
    isValidNew() {
      return this.$v.pass.newPassword.$error || this.validationNew
        ? "invalid"
        : this.pass.newPassword
        ? "valid"
        : null;
    },
    userData() {
      return {
        userName: this.userName,
        userEmail: this.userEmail
      };
    },
    userName: {
      get() {
        return this.fullName;
      },
      set(value) {
        this.SET_USER_NAME(value);
        this.SET_USER(value);
      }
    },
    userEmail: {
      get() {
        return this.email;
      },
      set(value) {
        this.SET_USER_EMAIL(value);
      }
    },
    isValidName() {
      let error;
      if (this.$v.userData.$error && this.editable) {
        error = "invalid";
      } else if (!this.$v.userData.$error && this.editable) {
        error = "valid";
      } else if (!this.editable) {
        error = null;
      }
      return error;
    },
    isValidEmail() {
      return this.$v.userData.userEmail.$error &&
        (this.userData.userEmail || this.$v.userData.$error)
        ? "invalid"
        : this.userData.userEmail && this.editable
        ? "valid"
        : null;
    }
  },
  methods: {
    ...mapMutations(["SET_USER"]),
    ...mapMutations("user/checkoutErrors", ["CHECKOUT_FAILURE"]),
    ...mapMutations("user", ["SET_USER_NAME", "SET_USER_EMAIL"]),
    ...mapActions("user", [
      "refreshKeys",
      "changePassword",
      "editUser",
      "getUser"
    ]),
    setPassword() {
      this.$v.pass.$reset();
      this.changePassword([this.pass.oldPassword, this.pass.newPassword]);
    },
    saveUser() {
      const user = {
        name: this.userName,
        email: this.userEmail
      };
      this.$v.userData.$touch();
      if (!this.$v.userData.$invalid && !this.isValidBackend) {
        this.editable = !this.editable;
        this.editUser(user);
      }
    },
    check(val) {
      this.$v.pass[val].$touch();
      this.CHECKOUT_FAILURE([]);
    },
    reset() {
      this.editable = !this.editable;
      this.getUser();
    },
    handlePayment(e) {
      e.preventDefault();
      this.showPayment = true;
    },
    handleClose() {
      this.showPayment = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.fields {
  display: block;
}
</style>
