import http from "@/store/helpers/http";
import checkoutErrors from "@/store/modules/checkoutErrors";
import {
  SET_USER,
  SET_USER_NAME,
  SET_USER_EMAIL,
  SET_USER_BALANCE,
  SET_USER_API_KEY,
  SET_USER_SECRET_KEY,
  SET_USER_RATE_NAME,
  SET_USER_RATE_LIMIT,
  SET_TOKEN,
  CHECKOUT_FAILURE,
  CHECKOUT_SUCCESS,
  SET_LEGAL
} from "@/store/mutation-types";

const state = {
  fullName: "",
  email: "",
  balance: "",
  apiKey: "",
  secretKey: "",
  rateName: "",
  rateLimit: 0,
  legal: {}
};
const getters = {
  inn: state => (state.legal ? state.legal.inn : ""),
  founder_fio: state => (state.legal ? state.legal.founder_fio : ""),
  kpp: state => (state.legal ? state.legal.kpp : ""),
  ogrn: state => (state.legal ? state.legal.ogrn : ""),
  registration_date: state =>
    state.legal ? state.legal.registration_date : "",
  registration_address: state =>
    state.legal ? state.legal.registration_address : ""
};
const actions = {
  async getUser({ commit }) {
    try {
      const response = await http.get("/user/");
      const {
        full_name,
        email,
        balance,
        api_key,
        secret_key,
        rate,
        legal
      } = response.data;
      commit(SET_USER, full_name, {
        root: true
      });
      commit(SET_USER_NAME, full_name);
      commit(SET_USER_EMAIL, email);
      commit(SET_USER_BALANCE, balance);
      commit(SET_USER_API_KEY, api_key);
      commit(SET_USER_SECRET_KEY, secret_key);
      commit(SET_LEGAL, legal);
      if (rate) {
        commit(SET_USER_RATE_NAME, rate.name);
        commit(SET_USER_RATE_LIMIT, rate.limit);
      }
    } catch (error) {
      console.error(error);
    }
  },
  async refreshKeys({ commit }) {
    try {
      const response = await http.get("/user/api-keys-refresh/");
      const { api_key, secret_key } = response.data;
      commit(SET_USER_API_KEY, api_key);
      commit(SET_USER_SECRET_KEY, secret_key);
    } catch (error) {
      console.error(error);
    }
  },
  async changePassword({ commit }, passwords) {
    try {
      const formData = new FormData();
      const [old_password, new_password] = passwords;
      formData.set("old_password", old_password);
      formData.set("new_password", new_password);

      const response = await http.post("/user/password-change-auth/", formData);
      const token = response.data.auth_token;

      commit(SET_TOKEN, token, {
        root: true
      });
      commit(`checkoutErrors/${CHECKOUT_SUCCESS}`, []);
    } catch (error) {
      commit(`checkoutErrors/${CHECKOUT_FAILURE}`, error.response.data);
      console.error(error);
    }
  },
  async editUser({ commit }, user) {
    try {
      const formData = new FormData();
      const { name, email } = user;
      formData.set("full_name", name);
      formData.set("email", email);
      await http.post("/user/edit/", formData);
      commit(SET_USER, name, {
        root: true
      });
      commit(SET_USER_NAME, name);
      commit(SET_USER_EMAIL, email);
    } catch (error) {
      commit(`checkoutErrors/${CHECKOUT_FAILURE}`, error.response.data);
      console.error(error);
    }
  }
};
const mutations = {
  [SET_USER_NAME](state, payload) {
    state.fullName = payload;
  },
  [SET_USER_EMAIL](state, payload) {
    state.email = payload;
  },
  [SET_USER_BALANCE](state, payload) {
    state.balance = payload;
  },
  [SET_USER_API_KEY](state, payload) {
    state.apiKey = payload;
  },
  [SET_USER_SECRET_KEY](state, payload) {
    state.secretKey = payload;
  },
  [SET_USER_RATE_NAME](state, payload) {
    state.rateName = payload;
  },
  [SET_USER_RATE_LIMIT](state, payload) {
    state.rateLimit = payload;
  },
  [SET_LEGAL](state, payload) {
    state.legal = payload;
  }
};

const modules = {
  checkoutErrors
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
  modules
};
