import http from "@/store/helpers/http";
import router from "@/router";
import checkoutErrors from "@/store/modules/checkoutErrors";
import {
  SET_TOKEN,
  CHECKOUT_FAILURE,
  CHECKOUT_SUCCESS
} from "@/store/mutation-types";
import checkout from "@/store/helpers/errorCheckout";

const state = {};

const getters = {};

const actions = {
  async registration({ commit, rootState }, user) {
    try {
      const { name, email, password } = user;
      const formData = new FormData();
      formData.set("full_name", name);
      formData.set("email", email);
      formData.set("password", password);
      const response = await http.post(
        "http://localhost:8000/user/registration/",
        formData
      );
      const token = response.data.auth_token;

      commit(SET_TOKEN, token, { root: true });
      commit(`checkoutErrors/${CHECKOUT_SUCCESS}`, []);

      if (rootState.tokenHandling.token) {
        router.push("/user-profile");
      }
    } catch (error) {
      checkout(error.response.data, errors => {
        commit(`checkoutErrors/${CHECKOUT_FAILURE}`, errors);
      });
      console.error(error);
    }
  }
};

const mutations = {};

const modules = {
  checkoutErrors
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
  modules
};
