<template>
  <d-container fluid class="main-content-container px-4">
    <div class="mb-4">
      <div class="row page-header py-4 no-gutters">
        <div class="text-center text-sm-left mb-4 mb-sm-0 col-sm-4 col">
          <h3 class="page-title">Реквизиты для акта</h3>
        </div>
      </div>
      <d-row>
        <d-col lg="12">
          <d-card>
            <d-card-body title="Реквизиты для договора и акта">
              <d-form>
                <d-form-row>
                  <d-col lg="12">
                    <d-form-row>
                      <d-col
                        md="12"
                        class="form-group"
                        :class="{ 'form-group--error': $v.form.item.$error }"
                      >
                        <label for="item">Наименование </label> <br />
                        <d-input
                          id="item"
                          v-model.trim="$v.form.item.$model"
                          type="text"
                          :state="validateItem()"
                          placeholder="ООО Мегакорп"
                          @input="setItem($event)"
                          @focus.native="onFocusItem"
                          @blur.native="onBlurItem"
                        />
                        <div v-if="!focus.item && state.item != null" class="">
                          <div
                            v-if="!$v.form.item.required"
                            class="validate-p-red"
                          >
                            Поле обязательно для заполнения
                          </div>
                          <div
                            v-if="!$v.form.item.alpha_ru"
                            class="validate-p-red"
                          >
                            Допустима только кириллица
                          </div>
                        </div>
                      </d-col>

                      <d-col
                        sm="12"
                        md="6"
                        class="form-group"
                        :class="{ 'form-group--error': $v.form.item.$error }"
                      >
                        <label for="inn">ИНН</label>
                        <d-input
                          id="inn"
                          v-model.trim="$v.form.inn.$model"
                          type="text"
                          pattern="[0-9]{10}([0-9]{2})?"
                          :state="validateInn()"
                          placeholder="1234567890"
                          @input="setInn($event)"
                          @focus.native="onFocusInn"
                          @blur.native="onBlurInn"
                        />
                        <div v-if="!focus.inn && state.inn != null" class="">
                          <div
                            v-if="!$v.form.inn.required"
                            class="validate-p-red"
                          >
                            Поле обязательно для заполнения
                          </div>
                          <div
                            v-if="!$v.form.inn.numeric"
                            class="validate-p-red"
                          >
                            Допустимы только числа
                          </div>
                          <div
                            v-if="!$v.form.inn.minLength && $v.form.inn.numeric"
                            class="validate-p-red"
                          >
                            ИНН может иметь только
                            {{ $v.form.inn.$params.minLength.min }} цифр
                          </div>
                          <div
                            v-if="!$v.form.inn.maxLength && $v.form.inn.numeric"
                            class="validate-p-red"
                          >
                            ИНН может иметь только
                            {{ $v.form.inn.$params.maxLength.max }} цифр
                          </div>
                        </div>
                      </d-col>
                      <d-col
                        sm="12"
                        md="6"
                        class="form-group"
                        :class="{ 'form-group--error': $v.form.item.$error }"
                      >
                        <label for="kpp">КПП</label>
                        <d-input
                          id="kpp"
                          v-model.trim="$v.form.kpp.$model"
                          type="text"
                          :state="validateKpp()"
                          pattern="^$|[0-9]{9}"
                          placeholder="123456789"
                          @input="setKpp($event)"
                          @focus.native="onFocusKpp"
                          @blur.native="onBlurKpp"
                        />
                        <div v-if="!focus.kpp && state.kpp != null" class="">
                          <div
                            v-if="!$v.form.kpp.required"
                            class="validate-p-red"
                          >
                            Поле обязательно для заполнения
                          </div>
                          <div
                            v-if="!$v.form.kpp.numeric"
                            class="validate-p-red"
                          >
                            Допустимы только числа
                          </div>
                          <div
                            v-if="!$v.form.kpp.minLength && $v.form.kpp.numeric"
                            class="validate-p-red"
                          >
                            КПП может иметь только
                            {{ $v.form.kpp.$params.minLength.min }} цифр
                          </div>
                          <div
                            v-if="!$v.form.kpp.maxLength && $v.form.kpp.numeric"
                            class="validate-p-red"
                          >
                            КПП может иметь только
                            {{ $v.form.kpp.$params.maxLength.max }} цифр
                          </div>
                        </div>
                      </d-col>
                      <d-col
                        md="12"
                        class="form-group"
                        :class="{ 'form-group--error': $v.form.item.$error }"
                      >
                        <label for="face">Контактное лицо</label>
                        <d-input
                          id="face"
                          v-model.trim="$v.form.face.$model"
                          type="text"
                          placeholder="Елена Панова"
                          :state="validateFace()"
                          @input="setFace($event)"
                          @focus.native="onFocusFace"
                          @blur.native="onBlurFace"
                        />
                        <div v-if="!focus.face && state.face != null" class="">
                          <div
                            v-if="!$v.form.face.required"
                            class="validate-p-red"
                          >
                            Поле обязательно для заполнения
                          </div>
                          <div
                            v-if="!$v.form.face.alpha_ru"
                            class="validate-p-red"
                          >
                            Допустима только кириллица
                          </div>
                        </div>
                      </d-col>
                      <d-col
                        md="12"
                        class="form-group"
                        :class="{ 'form-group--error': $v.form.item.$error }"
                      >
                        <label for="phoneNumber">Телефон</label>
                        <d-input
                          id="phoneNumber"
                          v-model.trim="$v.form.phoneNumber.$model"
                          v-mask="'+# (###) ###-##-##'"
                          type="text"
                          placeholder="79859575323"
                          :state="validatePhone()"
                          @input="setPhone($event)"
                          @focus.native="onFocusPhone"
                          @blur.native="onBlurPhone"
                        />
                        <div
                          v-if="!focus.phoneNumber && state.phoneNumber != null"
                          class=""
                        >
                          <div
                            v-if="!$v.form.phoneNumber.required"
                            class="validate-p-red"
                          >
                            Поле обязательно для заполнения
                          </div>
                          <div
                            v-if="
                              !$v.form.phoneNumber.isPhone &&
                                $v.form.phoneNumber.$model.length > 0
                            "
                            class="validate-p-red"
                          >
                            Введите корректный номер телефона
                          </div>
                        </div>
                      </d-col>
                      <d-col
                        md="12"
                        class="form-group"
                        :class="{ 'form-group--error': $v.form.item.$error }"
                      >
                        <label for="address">Адрес доставки</label>
                        <d-input
                          id="address"
                          v-model.trim="$v.form.address.$model"
                          name="address"
                          placeholder="г. Москва, ул. Алтуфьевское шоссе 87"
                          :state="validateAddress()"
                          @input="setAddress($event)"
                          @focus.native="onFocusAddress"
                          @blur.native="onBlurAddress"
                        />
                        <div
                          v-if="!focus.address && state.address != null"
                          class=""
                        >
                          <div
                            v-if="!$v.form.address.required"
                            class="validate-p-red"
                          >
                            Поле обязательно для заполнения
                          </div>
                        </div>
                      </d-col>
                      <d-col md="12" class="form-group">
                        <d-button
                          v-if="
                            validateItem() &&
                              validateInn() &&
                              validateKpp() &&
                              validateFace() &&
                              validatePhone() &&
                              validateAddress()
                          "
                          outline
                          theme="primary"
                          class="mb-2 mr-1"
                          type="button"
                          @click="
                            changePaymentDetails({
                              name: form.item,
                              inn: form.inn,
                              kpp: form.kpp,
                              face: form.face,
                              phoneNumber: form.phoneNumber,
                              address: form.address
                            });
                            refreshData();
                          "
                        >
                          Сохранить
                        </d-button>
                        <d-button
                          v-else
                          outline
                          disabled
                          theme="primary"
                          class="mb-2 mr-1"
                          type="button"
                        >
                          Сохранить
                        </d-button>
                      </d-col>
                    </d-form-row>
                  </d-col>
                </d-form-row>
              </d-form>
            </d-card-body>
          </d-card>
        </d-col>
      </d-row>
    </div>
  </d-container>
</template>

<script>
import { mapState, mapActions } from "vuex";
import {
  required,
  minLength,
  maxLength,
  numeric,
  alpha,
  helpers
} from "vuelidate/lib/validators";
const alpha_ru = helpers.regex("alpha", /^[а-яА-ЯёЁ\s]*$/);
const isPhone = value =>
  /^(\+7|7|8)?[\s\-]?\(?[489][0-9]{2}\)?[\s\-]?[0-9]{3}[\s\-]?[0-9]{2}[\s\-]?[0-9]{2}$/gm.test(
    value
  );

export default {
  data() {
    return {
      state: {
        item: null,
        inn: null,
        kpp: null,
        face: null,
        phoneNumber: null,
        address: null
      },
      form: {
        item: "",
        inn: "",
        kpp: "",
        face: "",
        phoneNumber: "",
        address: ""
      },
      focus: {
        item: false,
        inn: false,
        kpp: false,
        face: false,
        phoneNumber: false,
        address: false
      },
      bindProps: {
        mode: "international",
        defaultCountry: "RU",
        disabledFetchingCountry: false,
        disabled: false,
        disabledFormatting: false,
        placeholder: "(999)-888-77-66",
        required: true,
        enabledCountryCode: false,
        enabledFlags: true,
        preferredCountries: ["AU", "BR"],
        onlyCountries: [],
        ignoredCountries: [],
        autocomplete: "off",
        name: "telephone",
        maxLen: 25,
        wrapperClasses: "",
        inputClasses: "",
        dropdownOptions: {
          disabledDialCode: false
        },
        inputOptions: {
          showDialCode: false
        }
      }
    };
  },
  validations: {
    form: {
      item: {
        required,
        alpha_ru
      },
      inn: {
        required,
        minLength: minLength(10),
        maxLength: maxLength(10),
        numeric
      },
      kpp: {
        required,
        minLength: minLength(9),
        maxLength: maxLength(9),
        numeric
      },
      face: {
        required,
        alpha_ru
      },
      phoneNumber: {
        required,
        isPhone
      },
      address: {
        required
      }
    }
  },
  methods: {
    ...mapActions("userPaymentDetails", ["changePaymentDetails"]),
    // item case
    setItem(value) {
      this.form.item = value;
      this.$v.form.item.$touch();
    },
    onFocusItem() {
      this.focus.item = true;
      this.state.item = null;
    },
    onBlurItem() {
      if (this.$v.form.item.required && this.$v.form.item.alpha_ru) {
        this.focus.item = false;
        this.state.item = true;
      } else {
        this.focus.item = false;
        this.state.item = false;
      }
    },
    validateItem() {
      return (
        this.state.item &&
        this.$v.form.item.required &&
        this.$v.form.item.alpha_ru
      );
    },

    // inn case
    setInn(value) {
      this.form.inn = value;
      this.$v.form.inn.$touch();
    },
    onFocusInn() {
      this.focus.inn = true;
      this.state.inn = null;
    },
    onBlurInn() {
      if (
        this.$v.form.inn.required &&
        this.$v.form.inn.numeric &&
        this.$v.form.inn.minLength &&
        this.$v.form.inn.maxLength
      ) {
        this.focus.inn = false;
        this.state.inn = true;
      } else {
        this.focus.inn = false;
        this.state.inn = false;
      }
    },
    validateInn() {
      return (
        this.state.inn &&
        this.$v.form.inn.required &&
        this.$v.form.inn.numeric &&
        this.$v.form.inn.minLength &&
        this.$v.form.inn.maxLength
      );
    },

    // kpp case
    setKpp(value) {
      this.form.kpp = value;
      this.$v.form.kpp.$touch();
    },
    onFocusKpp() {
      this.focus.kpp = true;
      this.state.kpp = null;
    },
    onBlurKpp() {
      if (
        this.$v.form.kpp.required &&
        this.$v.form.kpp.numeric &&
        this.$v.form.kpp.minLength &&
        this.$v.form.kpp.maxLength
      ) {
        this.focus.kpp = false;
        this.state.kpp = true;
      } else {
        this.focus.kpp = false;
        this.state.kpp = false;
      }
    },
    validateKpp() {
      return (
        this.state.kpp &&
        this.$v.form.kpp.required &&
        this.$v.form.kpp.numeric &&
        this.$v.form.kpp.minLength &&
        this.$v.form.kpp.maxLength
      );
    },

    // face case
    setFace(value) {
      this.form.face = value;
      this.$v.form.face.$touch();
    },
    onFocusFace() {
      this.focus.face = true;
      this.state.face = null;
    },
    onBlurFace() {
      if (this.$v.form.face.required && this.$v.form.face.alpha_ru) {
        this.focus.face = false;
        this.state.face = true;
      } else {
        this.focus.face = false;
        this.state.face = false;
      }
    },
    validateFace() {
      return (
        this.state.face &&
        this.$v.form.face.required &&
        this.$v.form.face.alpha_ru
      );
    },

    // phone case
    setPhone(value) {
      this.form.phoneNumber = value;
      this.$v.form.phoneNumber.$touch();
    },
    onFocusPhone() {
      this.focus.phoneNumber = true;
      this.state.phoneNumber = null;
    },
    onBlurPhone() {
      if (
        this.$v.form.phoneNumber.required &&
        this.$v.form.phoneNumber.isPhone
      ) {
        this.focus.phoneNumber = false;
        this.state.phoneNumber = true;
      } else {
        this.focus.phoneNumber = false;
        this.state.phoneNumber = false;
      }
    },
    validatePhone() {
      return (
        this.state.phoneNumber &&
        this.$v.form.phoneNumber.required &&
        this.$v.form.phoneNumber.isPhone
      );
    },

    // address case
    setAddress(value) {
      this.form.address = value;
      this.$v.form.address.$touch();
    },
    onFocusAddress() {
      this.focus.address = true;
      this.state.address = null;
    },
    onBlurAddress() {
      if (this.$v.form.address.required) {
        this.focus.address = false;
        this.state.address = true;
      } else {
        this.focus.address = false;
        this.state.address = false;
      }
    },
    validateAddress() {
      return this.state.address && this.$v.form.address.required;
    },

    parseObject(object, value) {
      for (let element in object) {
        object[element] = value;
      }
    },
    refreshData() {
      this.parseObject(this.state, null);
      this.parseObject(this.form, "");
      this.parseObject(this.focus, false);
    }
  }
};
</script>

<style lang="css" scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.validate-p-red {
  margin: 0;
  padding: 0;
  color: #c4183c;
  height: 10px;
}

.page-title {
  white-space: nowrap;
}

.form-group {
  height: 100px;
  margin-bottom: 0rem;
}
@media screen and (max-width: 454px) {
  .page-title {
    white-space: normal;
  }
}
</style>
